.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

table {
  margin-top: 10px;
  padding-left: 15px;
  padding-top: 10px;
}

th, td {
  border: 1px solid gray;
  padding: 5px;
}

td {
  height: 25px;
}

.need-check {
  background: yellow;
}

.not-check {
  background: darkseagreen;
}

.not-interested {
  background: red;
}

.table-g {
  min-width: 100px;
}

.appSetData {
  padding-top: 20px;
  font-size: 16px;
}

input {
  margin-bottom: 10px;
  margin-left: 10px;
  font-size: 16px;
  height: 26px;
}

select {
  font-size: 16px;
  height: 26px;
}

button {
  margin-bottom: 10px;
  display: inline-block;
  font-size: 16px;
  height: 26px;
}

.appSetData__A {
  width: 80%;
  display: inline-block;
}

.appSetData__G {
  width:60%;
}

.appSetData__F {
  width:80%;
}

.appSetData__G-limit {
  width: 10%;
  display: inline-block;
}

.appSetData__G-relation {
  display: inline-block;
  min-width: 5%;
}
